@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap');

html {
    font-family: 'Montserrat', sans-serif;
    font-size: 10px;
}

*, *::after, *::before {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
    color: #333;
}

p {
    font-size: 1.6rem;
}

small {
    font-size: 1.2rem;
}

.container {
    margin: 0 auto;
    max-width: 1000px;
    padding: 40px;
}

.title {
    font-size: 4.4rem;
    text-align: center;
}

.form {
    display: grid;
}

.label {
    font-weight: 600;
    font-size: 1.7rem;
    margin-bottom: 0.2rem;
    text-transform: uppercase;
    text-align: center;
}

.input {
    font-weight: 400;
    font-size: 1.6rem;
    padding: 0.5rem 2rem;
    line-height: 2.8rem;
    border-radius: 20px;
    border: 1px solid #ddd;
    margin-bottom: 1rem;
    outline: none;
}

.button {
    background-color: rgba(0,0,0,0.75);
    color: white;
    padding: 1rem 2rem;
    border: 1px solid rgba(0,0,0,0.75);
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 250ms;
    outline: none;

    &:hover {
        background-color: rgba(0,0,0,0.85);
    }
}


@media (min-width: 786px) {
    .form {
        grid-template-columns: auto 1fr auto;
        grid-gap: 1rem;
        align-items: center;
    }

    .input {
        margin-bottom: 0;
    }
}

.card-list {
    margin-top: 4rem;
    overflow: auto;
}

.card {
    padding: 2rem 4rem;
    border-radius: 10px;
    box-shadow: 1px 1px 5px rgba(0,0,0,0.25);
    margin-bottom: 2rem;
    background-color: white;
    max-width: 400px;
}

.card-title {
    margin-bottom: 0.5rem;
    font-size: 3.2rem;
}

.card-image {
    margin: 0 auto;
    display: block;
}